import React, { Component } from 'react';
import Slider from './Slider';
import Sidebar from './Sidebar';
import Articles from './Articles';


class Home extends Component {
    render() {

        return (
            <div id="home">

                <Slider
                
                    title="Restaurant Siglo XXI"
                    btn="Ir al blog"
                    size="slider-big"
                />
                

                <div className="center">
                    <div id="content">
                        <h1 className="subheader">TOP 5 Platos de comida</h1>
                        <Articles
                            home="true"
                        />
                    </div>

                    <Sidebar
                    

                    />

                </div>
            </div>

        );
    }
}

export default Home;